(function () {
	'use strict';

	angular
		.module('thrust-app')
		.controller('LoginController', LoginController);

	/** @ngInject */
	function LoginController($state, $cookies, logger, authService) {
		var vm = this;

		vm.showMenu = false;
		vm.user = '';
		vm.password = '';
		vm.loginSubmit = loginSubmit;

		initLogin();

		function initLogin() {
			vm.showMenu = false;
			console.log($cookies.getObject('thrust_connected'));

			if ($cookies.getObject('thrust_connected')) {
				$state.go('WelcomePage');
			}
		}
		
		function loginSubmit() {
			return authService.login(vm.user, vm.password).then(function() {
				initLogin();
			});
    	}
	}

})();
